<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <Loading :isLoading="isLoading" />
  <div class="card-body">
    <VForm v-slot="{ errors, meta }">
      <table class="table align-middle table-light table-striped-columns">
        <tbody>
          <!-- id -->
          <tr>
            <th scope="row" class="">會員帳號<br />ID</th>
            <td>
              {{ user.email }}
            </td>
          </tr>
          <!-- birth -->
          <tr>
            <th scope="row">生日<br />Birth</th>
            <td>{{ user.birth }}</td>
          </tr>
          <!-- name -->
          <tr>
            <th scope="row">姓名<br />Name</th>
            <td>
              <VField name="姓名" type="text" rules="required" v-model="user.username" />
              <ErrorMessage name="姓名" class="text-danger ms-3" />
            </td>
          </tr>
          <!-- gender -->
          <tr>
            <th scope="row">性別<br />Gender</th>
            <td>
              <VField name="gender" type="radio" value="男" v-model="user.gender" /> 男
              <VField name="gender" type="radio" value="女" v-model="user.gender" /> 女
              <VField name="gender" type="radio" value="不提供" v-model="user.gender" /> 不提供
              <ErrorMessage name="gender" />
            </td>
          </tr>
          <!-- phone -->
          <tr>
            <th scope="row">行動電話<br />Mobile</th>
            <td>
              <VField
                name="電話"
                type="text"
                :class="{ 'is-invalid': errors['電話'] }"
                :rules="isPhone"
                v-model="user.phone"
              />
              <ErrorMessage name="電話" class="text-danger ms-3" />
            </td>
          </tr>
          <!-- address -->
          <tr>
            <th scope="row">聯絡地址<br />Location</th>
            <td>
              <div class="">本地址將作為您日後購物預設寄送地址，請勿填寫郵政信箱</div>
              <div class="d-flex">
                <VField
                  name="city"
                  as="select"
                  class="form-select"
                  v-model="user.address.city"
                  @change="changeTown"
                >
                  <option value="" disabled>Select a town</option>
                  <template v-for="(item, id) in cityList" :key="id">
                    <option :value="item.name">{{ item.name }}</option>
                  </template>
                </VField>
                <VField
                  name="township"
                  as="select"
                  class="form-select ms-2"
                  v-model="user.address.town"
                  @change="changePostal"
                >
                  <option value="" disabled>Select a town</option>
                  <template v-for="item in townList" :key="item.CODE">
                    <option :value="item.name">{{ item.name }}</option>
                  </template>
                </VField>
                <input
                  type="text"
                  class="ms-2 col-1 text-center"
                  v-model="user.address.postalCode"
                  disabled
                />
                <VField name="地址" type="text" v-model="user.address.street" class="ms-2 col-7" />
              </div>
            </td>
          </tr>
        </tbody>
      </table>
      <div class="text-center">
        <button
          type="button"
          :class="{ 'btn-info': !meta.valid }"
          :disabled="!meta.valid"
          class="btn btn-dark"
          @click="$emit('modify', this.user)"
        >
          確認修改
        </button>
      </div>
    </VForm>
  </div>
</template>

<script>
import Loading from '@/components/models/LoadingModel.vue';

export default {
  inheritAttrs: false, // 移除不必要警告
  data() {
    return {
      cityList: [],
      townList: [],
      isLoading: false,
      user: {
        email: '',
        birth: '',
        username: '',
        gender: '',
        address: {
          city: '',
          town: '',
          postalCode: 300,
          street: '',
        },
      },
    };
  },
  props: ['userProfile'],
  watch: {
    userProfile() {
      this.user = this.userProfile;
    },
  },
  components: {
    Loading,
  },
  methods: {
    isPhone(value) {
      const phoneNumber = /^(09)[0-9]{8}$/;
      return phoneNumber.test(value) ? true : '需要正確的電話號碼';
    },
    getMap() {
      this.isLoading = true;
      const api = `${process.env.VUE_APP_API2}/api/map/getMap`;
      this.$http
        .get(api)
        .then((res) => {
          this.cityList = res.data.data;
          this.changeTown();
          this.isLoading = false;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    changeTown() {
      this.cityList.forEach((item) => {
        if (item.name === this.user.address.city) {
          this.townList = item.districts;
        }
      });
    },
    changePostal() {
      this.townList.forEach((item) => {
        if (item.name === this.user.address.town) {
          this.user.address.postalCode = item.zip;
        }
      });
    },
  },
  mounted() {
    this.getMap();
  },
};
</script>
