<script>
import Profile from '@/components/user/ProfileModel.vue';
import Chart from '@/components/user/ChartModel.vue';
import Order from '@/components/user/OrderModel.vue';
import Coupon from '@/components/user/CouponModel.vue';
import Password from '@/components/user/PasswordModel.vue';
import Logout from '@/components/user/LogoutModel.vue';

export default {
  data() {
    return {
      select: 'PROFILE',
      menuList: ['PROFILE', 'CHART', 'ORDER', 'COUPON', 'PASSWORD', 'LOGOUT'],
      user: {},
    };
  },
  components: {
    Profile,
    Chart,
    Order,
    Coupon,
    Password,
    Logout,
  },
  methods: {
    getUser() {
      const api = `${process.env.VUE_APP_API2}/api/user/profile`;
      this.$http
        .get(api)
        .then((res) => {
          this.user = res.data.data;
          this.isLogin = !this.isLogin;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    modifyUser(data) {
      const api = `${process.env.VUE_APP_API2}/api/user/profile`;
      this.$http
        .patch(api, data)
        .then((res) => {
          console.log(res);
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
  mounted() {
    this.getUser();
  },
};
</script>
